<template>
  <div id="downDetail">
    <fall-ground v-if="pageStates.fallGround"></fall-ground>
    <fall-house-resource></fall-house-resource>
  </div>
</template>

<script>
import fallGround from "@/components/commons/fallGround"
import fallHouseResource from "@/components/commons/fallHouseResource"
export default{
  name:"downDetail",
  components:{fallGround,fallHouseResource},
  data(){
    return{
      pageStates:{
        fallGround:false
      }
    }
  }

}
    
</script>

<style lang="less" scoped>
  #downDetail{
      background-color: #fff;
  }
</style>




